.form_back { 
    background: url(./form.jpg) no-repeat center center fixed; 
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }


  .form_back {
    text-align: center;
    margin: 0;
    padding: 0;
    height: 95vh;
  }

  #form_form{
      background-color: rgba(22, 22, 22, 0.5);
      padding: 100px;

  }

  .title{
      color: white;
      margin-bottom: 50px;
      font-size: 30px;
  }

  #next{
      position: absolute;
      right: 15%;
      top: 50%;
      border: none;
      background-color: none;
      background: none;
      font-size: 50px;
  }

  select{
    overflow:auto;
}

.form_select{
    color: white;
    list-style-type: none;

}

.form_option{
    color: black;
    padding: 10px;
    border: solid black 1px;
    background-color: white;
    margin: 5px;
    border-radius: 5px;
}

.form_option:hover{
    background-color: black;
    color: white;
}